<template>
  <div>
    <v-layout row justify-center >
      <v-dialog v-model="dialog" width="800px" :persistent="true" >
        <template v-slot:activator="{ on }">
          <!-- <v-btn color="primary" flat dark v-on="on" ></v-btn> -->
          <v-chip color="secondary" text-color="white" v-on="on" >
          <v-avatar>
            <v-icon>account_circle</v-icon>
          </v-avatar>
          {{ userData.user_name }}
        </v-chip>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline"> </span>
          </v-card-title>
          <v-card-text>
            <form>
              <!--  -->
              <v-text-field
                v-model="userData.user_name"
                type="text"
                maxlength="16"
                name="input-10-1"
                label=""
                hint
                counter
                disabled
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="userData.true_name"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="userData.role_name"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
                disabled
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="userData.email"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="userData.phone"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="gender"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
                disabled
              ></v-text-field>
              <!--  -->
              <v-text-field
                v-model="order_sn"
                type="text"
                name="input-10-2"
                maxlength="26"
                label=""
                hint
                counter
                disabled
              ></v-text-field>
              <p></p>
              <p>  {{ userData.create_time | time }}</p>
              <p> {{ userData.update_time | time }}</p>
              <i> * </i>
            </form>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat="flat" @click="dialog = false"></v-btn>
            <v-btn color="info darken-1" @click="user_modify"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

  </div>
</template>

<script>
import Api from "../../../server/api.js";
import { truncate } from 'fs';
import { setTimeout } from 'timers';
export default {
  data() {
    return {
      // 
      loader: false,
      userData: this.$cookie.getLocalStorage("root_path"),

      // 
      dialog: false
    };
  },
  computed: {
    gender(){
      return  this.userData.gender==1?'':'';
    },
    order_sn(){
      return  this.userData.order_sn==''?'':this.userData.order_sn;
    }
  },
  methods: {
      // 
    user_modify(){
       if( this.userData.true_name.length < 2 ){ this.$Dialog.getProductCheckResult_({text: ``,timeout: 3000, snackbar: true }); return false; }
       if( this.userData.email.length < 5 ){ this.$Dialog.getProductCheckResult_({text: ``,timeout: 3000, snackbar: true }); return false; }
       if( this.userData.phone.length !== 11 ){ this.$Dialog.getProductCheckResult_({text: ``,timeout: 3000, snackbar: true }); return false; }
       this.$axios({
        method: "post",
        url: Api.URL.manages_edit_manage,
        data: {
          email: this.userData.email,
          phone: this.userData.phone,
          true_name: this.userData.true_name
        }
      })
        .then(response => {
          //
          if (response.data.result === 0) {
            this.dialog = false;
            this.$Dialog.getProductCheckResult_({text: "",timeout: 3000, snackbar: true });
            setTimeout(()=>{
              this.$router.push({
                path: `/`
              });
            },2000)
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
            this.$Dialog.getProductCheckResult_({text: ''+error,timeout: 3000, snackbar: true });
        });
    }
  }
};
</script>

<style lang="less" scoped>
</style>
